import React, { useState, useEffect } from 'react';
import { sampleData } from '../../sampleData/sampleEvents';
import './style.scss';
import { IoIosArrowBack } from "react-icons/io";
import calendar from '../../assets/icon/ico_calendar-white.png';
import location from '../../assets/icon/ico_location-white.png';
import { Link } from 'react-router-dom';
import { IconContext } from "react-icons";
import { MdKeyboardArrowUp, MdKeyboardArrowDown } from "react-icons/md";
import moment from 'moment';

const Register = (props) => {
    console.log(props)
    const [displayBio, setdisplayBio] = useState(false);
    const [event, setEvent] = useState(null);

    useEffect(() => {

        sampleData.map(event => {
            event.start = new Date(event.startTime);
            event.end = new Date(event.endTime);
            event.startDate = moment(event.start).format("dddd, MMMM Do YYYY");
            event.eventStart = moment(event.start).format("HH:mm");
            event.eventEnd = moment(event.end).format("HH:mm");
            event.eventRange = `${event.eventStart} - ${event.eventEnd}`
            event.lang.map((reg, index) => {

                if (reg === 'en-my') {
                    event.lang[index] = ' English '
                } else if (reg === 'ms') {
                    event.lang[index] = ' Bahasa Melayu '
                } else if (reg === 'zh') {
                    event.lang[index] = ' Chinese '
                } //lang: ['en-my', 'ms', 'zh'], 'en-my' isn't object. [{'en-my'},{'ms'}], return 'English'
            });
        })

        const id = props.match.params.id
        console.log(id, parseInt(id)); //parseInt is convert id to integer
        let thisEvent = sampleData.find(e => e.id === parseInt(id)) //e.id === id is different data type, so need parseInt
        setEvent(thisEvent)
        console.log(thisEvent);
        console.log(sampleData)

    }, []); //first action after return 


    return (
        <>
            {event && <div className='event-register'>
                <div className='mobile-header'>
                    <Link to={`/module/${event.id}`}>
                        <IconContext.Provider value={{ size: "2em" }}>
                            <div className='arrow-back-transparent'><IoIosArrowBack /></div>
                        </IconContext.Provider>
                    </Link>
                    <div className='title'>
                        Register
                    </div>
                </div>
                <div className='event-details'>
                    <div className='event-wrapper'>
                        <div className='event-reg-card'>
                            <div className='event-reg-card-wrapper'>
                                <div className='event-reg-card-title'>{event.title}</div>
                                {displayBio ? (
                                    <IconContext.Provider value={{ size: "2em" }}>
                                        <div className='event-reg-card-toggle' onClick={() => setdisplayBio(false)}> <MdKeyboardArrowDown /> </div>
                                    </IconContext.Provider>
                                ) : (<div className='event-item'>
                                    <div className='event-reg-card-language'>{event.lang.join()}</div>
                                    <div className='event-reg-card-item'>
                                        <div className='icon'><img src={calendar} /></div>
                                        <div className='info'>{event.startDate}<br />{event.eventRange}</div>
                                    </div>
                                    <div className='event-reg-card-item'>
                                        <div className='icon'><img src={location} /></div>
                                        <div className='info'>Hosted virtually via Zoom</div>
                                    </div>
                                    <br /><IconContext.Provider value={{ size: "2em" }}>
                                        <div className='event-reg-card-toggle' onClick={() => setdisplayBio(true)}><MdKeyboardArrowUp /></div>
                                    </IconContext.Provider>
                                </div>)
                                }
                                <div className='ticket'>
                                    <div className='price'>FREE</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Link to={`/module/${event.id}/registered`}>
                    <div className='event-confirm-mobile'>
                        <div className='amway-button'>Confirm</div>
                    </div>
                </Link>
            </div>}

        </>
    )
}

export default Register;